/* PULSE BUBBLES */
.spinner-box{
    margin-top: 20px;
}
.pulse-container {
    width: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
  }
  
  .pulse-bubble {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #222222;
  }
  
  .pulse-bubble-1 {
      animation: pulse .4s ease 0s infinite alternate;
  }
  .pulse-bubble-2 {
      animation: pulse .4s ease .2s infinite alternate;
  }
  .pulse-bubble-3 {
      animation: pulse .4s ease .4s infinite alternate;
  }

  @keyframes pulse {
    from {
      opacity: 1;
      transform: scale(1.25);
    }
    to {
      opacity: .25;
      transform: scale(.75);
    }
  }